import {
	findUser,
	getAuList
} from '../../api/api.js'
import uniIcons from "@/components/uni-icons/uni-icons.vue"
import uniCard from '@/components/uni-card/uni-card.vue'
import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"

export default {
	components: {
		uniCard,
		uniSegmentedControl,
		uniIcons
	},
	data() {
		return {
			topicList: [],
			edit: false,
			user: {},
			page: 1,
			myList: [],
			totalNum: 0,
			display: true, //显示主页还是信息页，true为主页，false为信息页
			contentShow: true,
			auid: null,
			form: {
				userId: undefined,
				userName: null,
				sign: '签名是一种态度，我想我可以更酷',
				sex: '男',
				birth: '1998-12-21',
				place: '四川省成都市'
			},
			editForm: {
				userName: null,
				sign: null,
				sex: null,
				birth: null,
				place: null
			}
		}
	},
	onLoad(options) {
		this.findUser(options.id)
		this.auid = options.id
	},
	filters: {
		ellipsis(value, num) {
			if (!value || !num) return "";
			if (value.length >= num) {
				return value.slice(0, num) + "...";
			}
			return value;
		}
	},
	computed: {
		userId() {
			return this.$store.state.user._id
		}
	},
	methods: {
		submit() {
			this.form.userId = this.userId
			// var formdata=new window.FormData()
			// let infoFormList = []
			// for (let prop in this.form) {
			// 	infoFormList.push({
			// 		key: prop,
			// 		value: this.form[prop]
			// 	})
			// }
			// infoFormList = [{
			// 	key: 'file',
			// 	value: this.uploadFile
			// }, ...infoFormList]
			// infoFormList.forEach(el => {
			// 	formData.append(item.key, item.value)
			// })
			console.log(formData)
		},
		editBtn() {
			this.edit = true
			this.editForm = Object.assign({}, this.editForm, this.form)
		},
		infoShow() {
			this.display = !this.display
			this.contentShow = !this.contentShow
		},
		details() {},
		findUser(id) {
			findUser({
				id: id
			}).then(res => {
				if (res.data.status === 'success') {
					this.user = res.data.data
					this.form.userName = res.data.data.userName
					this.getList()
				}
			}).catch(err => {
				console.log(err)
			})
		},
		getList() {
			getAuList({
				userName: this.user.userName,
				page: this.page
			}).then(res => {
				if (res.data.status === 'success') {
					this.myList.push(...res.data.data)
					this.totalNum = res.data.total
					this.topicList = res.data.data.map(el => el.region).slice(0, 2)
				}
			}), err => {}
		},
		getMore() {
			this.page++
			this.getList()
		}
	}
}
